import React, { Component } from "react";

class Kaushal extends Component {
  render() {
    return (
      <div id="kaushal">
        <div className="heading">
          <svg viewBox="0 0 1350 700">
            <text
              className="text"
              textAnchor="end"
              x="83%"
              y="90%"
              fill="transparent"
              
            >
              Kaushal 2.0
            </text>
          </svg>
        </div>
      </div>
    );
  }
}

export default Kaushal;
